(function () {
	"use strict";

	angular.module("3drepo")
		.directive("accountMenu", accountMenu);

	function accountMenu() {
		return {
			restrict: "EA",
			templateUrl: "accountMenu.html",
			scope: {
				account: "="
			},
			controller: AccountMenuCtrl,
			controllerAs: "vm",
			bindToController: true
		};
	}

	AccountMenuCtrl.$inject = ["Auth", "EventService"];

	function AccountMenuCtrl (Auth, EventService) {
		var vm = this,
			promise;

		/**
		 * Open menu
		 *
		 * @param $mdOpenMenu
		 * @param ev
		 */
		vm.openMenu = function ($mdOpenMenu, ev) {
			$mdOpenMenu(ev);
		};

		/**
		 * Show user projects
		 */
		vm.showProjects = function () {
			EventService.send(EventService.EVENT.SHOW_PROJECTS);
		};

		/**
		 * Logout
		 */
		vm.logout = function () {
			Auth.logout();
		};

		vm.openUserManual = function(){
			window.open('http://3drepo.org/projects/3drepo-io-user-manual/', '_blank');
		};
	}
}());
